* {
  margin: 0;
  padding: 0;
}

body {
  color: #000;
  background-color: #fff;
  font-family: DM Sans;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 0 110px;
}

.list {
  list-style-type: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

.contacts__btn-franschise, .contacts__btn-location {
  text-align: center;
  border: none;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.contacts__btn-location {
  color: #fff;
  background-color: #d41443;
}

.contacts__btn-franschise {
  color: #d41443;
  background-color: #fff;
}

.contacts {
  background-image: linear-gradient(#fff 84px, #fff5f6 84px);
  font-family: DM Sans;
  font-weight: 500;
}

.contacts__container {
  padding-left: 20px;
  padding-right: 20px;
}

.contacts__title {
  display: none;
}

.contacts__item {
  flex-direction: column;
  gap: 20px;
  margin-bottom: 54px;
  display: flex;
}

.contacts__card {
  max-width: 220px;
  background-color: #fff;
  border-radius: 24px;
  margin: 0 auto;
  padding: 43px 21px 43px 20px;
  transition-property: scale;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 8px 30px #d414431a;
}

.contacts__name {
  width: 80px;
  height: 16px;
  letter-spacing: .04em;
  color: #fff;
  background-color: #f0d1a5;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;
  font-size: .75rem;
  line-height: 1.375rem;
  display: flex;
}

.contacts__name--foodtruck {
  width: 130px;
  background-color: #c2e297;
}

.contacts__city {
  letter-spacing: .04em;
  color: #000;
  margin-bottom: 10px;
  font-size: .75rem;
  line-height: 1.375rem;
}

.contacts__description {
  color: #907e82;
  margin-bottom: 25px;
  font-size: .75rem;
  line-height: 1.125rem;
}

.contacts__schedule {
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
  margin-bottom: 25px;
  padding-top: 25px;
  padding-bottom: 25px;
}

.contacts__schedule-item:first-child {
  margin-bottom: 10px;
}

.contacts__schedule-day {
  font-size: .75rem;
  line-height: 1.375rem;
}

.contacts__schedule-hour {
  color: #ffa5ba;
  font-size: .75rem;
  line-height: 1.375rem;
}

.contacts__mail, .contacts__tel {
  font-size: .75rem;
  line-height: 1, 125rem;
  color: #907e82;
}

.contacts__tel:hover, .contacts__mail:hover, .contacts__tel:focus, .contacts__mail:focus {
  color: #c7133e;
}

.contacts__card:hover {
  scale: 1.05;
}

.contacts__btn-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding-bottom: 113px;
  display: flex;
}

.contacts__btn-location, .contacts__btn-franschise {
  width: 161px;
  height: 44px;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.3125rem;
}

.footer {
  width: 100%;
  margin: auto;
}

.footer__firstArea {
  flex-wrap: wrap;
  justify-content: space-between;
  margin: auto;
  display: flex;
}

.footer__firstAreaTitleArea {
  min-width: 14.625rem;
  font-family: Titan One;
}

.footer__firstAreaTitle {
  text-transform: uppercase;
  color: #ffa5ba;
  padding-bottom: 1rem;
  font-size: 1.125rem;
  line-height: 1.16;
}

.footer__firstAreaSubtitle {
  text-transform: uppercase;
  color: #d41443;
  font-size: 2.125rem;
  line-height: 1.15;
}

.footer__socialsContact {
  flex-flow: column wrap;
  align-items: flex-end;
  padding-top: 2.6875rem;
  padding-bottom: 2.6875rem;
  padding-left: 4.375rem;
  display: flex;
}

.footer__socialsArea {
  gap: 1.75rem;
  display: flex;
}

.footer__socialsSubArea {
  background-color: #ffa5ba;
  border-radius: 50%;
  transition: all .25s;
}

.footer__socialsSubArea:hover, .footer__socialsSubArea:focus {
  background-color: #d41443;
}

.footer__iconArea {
  height: 2rem;
  width: 2rem;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__icon {
  color: #fff;
  fill: currentColor;
}

.footer__ContactInfo {
  padding-top: 3rem;
  font-size: .75rem;
  font-weight: 700;
  line-height: 1.86;
}

.footer__ContactMessage {
  display: inline-block;
}

.footer__phoneLink {
  color: #d41443;
  text-decoration: none;
  display: inline-block;
}

.footer__secondArea {
  width: 100%;
  border-top: 1px solid #e1e1e1;
}

.footer__secondAreaDiv2 {
  color: #907e82;
  font-size: .875rem;
  font-weight: 700;
}

@media (max-width: 767px) {
  .footer__firstArea {
    max-width: 20rem;
  }

  .footer__firstAreaTitleArea {
    max-width: 14.625rem;
    padding-left: 1.25rem;
  }

  .footer__firstAreaTitle {
    padding-left: .125rem;
  }

  .footer__secondAreaDiv {
    max-width: 9.375rem;
    margin: auto;
    padding-top: 1.375rem;
    padding-bottom: 1.5625rem;
  }

  .footer__secondAreaDiv2 {
    max-width: 9.25rem;
    text-align: center;
  }
}

@media (min-width: 768px) and (max-width: 1199px) {
  .footer__firstArea {
    max-width: 48rem;
  }

  .footer__firstAreaTitleArea {
    max-width: 23.5rem;
    padding-bottom: 5.6875rem;
    padding-left: 2.25rem;
    font-size: 1.625rem;
  }

  .footer__firstAreaTitle {
    font-size: 1.625rem;
  }

  .footer__firstAreaSubtitle {
    font-size: 3rem;
  }

  .footer__socialsContact {
    padding: 0 2.25rem 5.6875rem 0;
  }

  .footer__ContactInfo {
    font-size: .875rem;
  }

  .footer__secondAreaDiv {
    display: flex;
  }

  .footer__secondAreaDiv2 {
    margin: auto;
    padding-top: 1.8125rem;
    line-height: 1.86;
  }
}

@media (min-width: 1200px) {
  .footer__firstArea {
    max-width: 80rem;
  }

  .footer__firstAreaTitleArea {
    max-width: 36.75rem;
    padding-bottom: 6rem;
    padding-left: 7.125rem;
  }

  .footer__firstAreaTitle {
    font-size: 1.875rem;
  }

  .footer__firstAreaSubtitle {
    font-size: 3.625rem;
  }

  .footer__socialsContact {
    padding: 0 7.5625rem 5.6875rem 0;
  }

  .footer__ContactInfo {
    font-size: 1rem;
  }

  .footer__secondAreaDiv {
    display: flex;
  }

  .footer__secondAreaDiv2 {
    margin: auto;
    padding-top: 1.8125rem;
    line-height: 1.86;
  }
}

